import { Component } from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { AuthService } from './services/auth.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {

  public errors = [];

  public userData = {
    email: "",
    password: "",
    username: "",
    rememberMe: false
  }

  public isLoggedIn : boolean = false;

  constructor(private authService: AuthService,
    private router: Router){
    
      let base64str = localStorage.getItem("credentials");
      if(base64str != undefined){
        let credentials = atob(base64str).split(":");
        this.userData.email = credentials[0];
        this.userData.password = credentials[1];
        this.userData.rememberMe = true;
      }
  }


  public registerUser() {
    this.authService.registerUser(this.userData)
      .subscribe(
        res => {
          console.log(res);
          this.closeModals();
          this.openLogin();
          this.errors = [];
        },
        err => this.errors = err.error
      )
  }

  public loginUser(){
    this.authService.loginUser(this.userData)
      .subscribe(
        res => {
          console.log(res);
          this.closeModals();
          localStorage.setItem('token', res.token);
          this.isLoggedIn = true;
          this.errors = [];
          this.router.navigate(['/dashboard']);
        },
        err => this.errors = [err.error] 
      )
  }

  openLogin() {
    setTimeout(() => {
      document.getElementById("login-modal").style.display = "block";
    }, 50);
  }

  openSingup() {

    if(this.isLoggedIn){
      this.isLoggedIn = false;
      localStorage.removeItem('token');
      this.router.navigate(['/login']);
      return;
    }

    setTimeout(() => {
      document.getElementById("signup-modal").style.display = "block";
    }, 50);
  }

  closeModals() {
    document.getElementById("signup-modal").style.display = "none";
    document.getElementById("login-modal").style.display = "none";
  }

  loginClicked(type) {

    if (type == "facebook") {
      alert("not yet implemented!");
      return;
    }

    if (type == "strava") {
      alert("not yet implemented!");
      return;
    }

    if (type == "email") {

      if(this.userData.rememberMe){
        let credentials = this.userData.email + ":" + this.userData.password;
        localStorage.setItem('credentials', btoa(credentials));
      }

      this.loginUser();
      return;
    }

  }

  signupClicked(type) {

    if (type == "facebook") {
      alert("not yet implemented!");
      return;
    }

    if (type == "strava") {
      alert("not yet implemented!");
      return;
    }

    if (type == "email") {
      this.registerUser();
      return;
    }
  }

   
}
